}

.uikit-body dl > dt {
  font-weight: bold;
  margin-top: 0.8em;
  margin-bottom: 0.8em;
}

.uikit-body dl > dt:first-of-type {
  margin-top: 0;
}

.uikit-body em,
.uikit-body i {
  font-style: italic;
}

.uikit-body strong {
  font-weight: bold;
}

.uikit-body small {
  font-size: 87.5%;
  color: #464646;
}

.uikit-body s {
  text-decoration: line-through;
}

.uikit-body del {
  text-decoration: line-through;
  color: #5a5a5a;
}

.uikit-body ins {
  text-decoration-line: underline;
  text-decoration-style: dashed;
}

.uikit-body dfn {
  font-style: normal;
