  position: relative;
  vertical-align: baseline;
}

.uikit-body sub {
  bottom: -0.25em;
}

.uikit-body sup {
  top: -0.5em;
}

.uikit-body code,
.uikit-body kbd,
.uikit-body pre {
  font-family: "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
