}

.uikit-body kbd {
  display: inline-block;
  font-size: 14px;
  border: solid 1px #8cbcc6;
  border-radius: 3px;
  box-shadow: 0 1px 0 0 #00bfe9;
  background-color: #fefefe;
  text-align: center;
  padding: 0 0.8em;
  margin: 0 2px;
}

.uikit-body hr {
  box-sizing: content-box;
  height: 0;
